import { fetchErrorDetails} from "../../services/error";
import { getExecutionsByErrorId } from "../../services/request";
var _ = require("lodash");
import moment from "moment";

export default {
  name: "home",
  data() {
    return {
      textFilter: "",
      selectedTab: "errorDetailsTab",
      fetchingData: false,
      fetchExecutionsData: false,
      sessionId: null,
      errorId: null,
      executionList: [],
      errorDetails: null,

      // pagination variable
      total: null,
      currentPage: 1,
			currentPageSize: 50
    };
  },
  computed: {
    isErrorDetailAvailable: function () {
      return this.errorDetails && this.errorDetails.stackTrace;
    },
    isStackTraceAvailable: function () {
      return this.errorDetails && this.errorDetails.stackTrace && this.errorDetails.stackTrace.length > 0;
    }
  },
  methods: {
    // pagination methods start
    handleSizeChange(val) {
			this.currentPageSize = val;
			// console.log(this.currentPageSize);
			this.fetchExecutions();
		},
		handleCurrentChange(val) {
			this.currentPage = val;
			// console.log(this.currentPage);
			this.fetchExecutions();
    },
    // pagination methods end

    handleClick(tab, event) {
      if (this.selectedTab == "executionListTab") {
        this.fetchExecutions();
      } 
      // else if (this.selectedTab == "server_logs") {
      //   this.fetchServerLogByRequest();
      // } 
    },
    getExecutionDetailsLink(reqId, timestamp) {
      return '/api/execution/detail?id=' + reqId + '&timestamp=' + timestamp;
    },
    getRequestDisplayDate(date) {
      var dt = moment(date);
      // console.log(dt.toString());
      return dt.format("MMMM Do, h:m:ss A");
    },
    fetchErrorDetails() {
      var params = {
        errorId: this.errorId
      };
      this.fetchingData = true;
      fetchErrorDetails(params)
        .then(result => {
          if (result.data.success) {
            this.errorDetails = result.data.data;
            this.fetchingData = false;
          } else {
            //this.$toast.error(result.data.message);
            this.fetchingData = false;
          }
        })
        .catch(error => {
          this.error('Internal Error.');
          this.fetchingData = false;
        });
    },
    fetchExecutions() {
      var params = {
        errorId: this.errorId
      };
      this.fetchExecutionsData = true;
      getExecutionsByErrorId(params)
        .then(result => {
          if (result.data.success) {
            this.executionList = result.data.data;
            this.total = result.data.pagination.count;
            this.fetchExecutionsData = false;
          } else {
            //this.$toast.error(result.data.message);
            this.fetchExecutionsData = false;
          }
        })
        .catch(error => {
          this.error('Internal Error.');
          this.fetchExecutionsData = false;
        });
    },
  },
  created() {
    this.errorId = this.$route.query.id;
    this.fetchErrorDetails();
  }
};



