const axios = require('axios');
const { baseUrl } = require("./settings.js");

const fetchRequests = function (params) {
    return axios.post(baseUrl + 'getReqList', params);
}

const fetchGraphRequests = function (params) {
    return axios.post(baseUrl + 'getGraphReqList', params);
}

const fetchExecutionList = function (params) {
    return axios.post(baseUrl + 'executionList', params);
}

const fetchExecutionListForSession = function (params) {
    return axios.post(baseUrl + 'executionListForSession', params);
}

const fetchExecutionDetails = function (params) {
    return axios.post(baseUrl + 'getExecutionDetails', params);
}

const getExecutionListGraphData = function (params) {
    return axios.post(baseUrl + 'getGraphExecutionList', params);
}

const getExecutionsByErrorId = function (params) {
    return axios.post(baseUrl + 'getExecutionsByErrorId', params);
}

module.exports = {
    fetchRequests,
    fetchGraphRequests,
    fetchExecutionList,
    fetchExecutionListForSession,
    fetchExecutionDetails,
    getExecutionListGraphData,
    getExecutionsByErrorId
}


