const axios = require('axios');
const { baseUrl } = require("./settings.js");

const fetchErrors = function (params) {
    return axios.post(baseUrl + 'getErrorList', params);
}

const fetchErrorDetails = function (params) {
    return axios.post(baseUrl + 'getErrorDetail', params);
}

module.exports = {
    fetchErrors,
    fetchErrorDetails,
}


