<template>
	<div id="errorDetailsPage" class="container">
	
		<!-- Header -->
		<div class="card">
			<div class="card-header">
				<el-breadcrumb separator-class="el-icon-arrow-right">
					<el-breadcrumb-item :to="{ path: '/error' }">Error</el-breadcrumb-item>
					<el-breadcrumb-item>Details</el-breadcrumb-item>
				</el-breadcrumb>
			</div>
	
			<div v-if="!errorDetails" v-loading="true" style="background: white; height: 300px;">
			</div>
	
			<div class="card-body errorDetailsHeader" v-if="errorDetails">
	
				<!-- API Details -->
				<div class="row apiInfoHeader">
					<div class="col-md-8" v-if="isErrorDetailAvailable">
						<div style="color: red; font-size: 18px;">{{errorDetails.message}}</div>
						<div style="color: #777; font-size: 12px; margin-top: -5px;">
							{{errorDetails.fileName + ':' + errorDetails.lineNumber}}
						</div>
					</div>
					<div class="col-md-8" v-else>
						<div style="color: red; font-size: 18px;">{{errorDetails.message}}</div>
					</div>
					<div class="col-md-4" style="text-align: right; font-size: 15px;">
						<div> {{errorDetails.total}} Events</div>
						<div> {{getEventDisplayDate(errorDetails.timestamp)}}, {{getEventDisplayTime(errorDetails.timestamp)}}</div>
					</div>
	
				</div>
	
			</div>
		</div>
	
		<!-- Tab -->
		<div class="errorDetails" v-if="errorDetails">
			<el-tabs v-model="selectedTab" @tab-click="handleClick" type="border-card">
	
				<!-- Error Details -->
				<el-tab-pane class="errorDetailsTab" v-loading="fetchingData" label="Error Details" name="errorDetailsTab">
					<div v-if="isErrorDetailAvailable && isStackTraceAvailable">
						<!-- Show Code -->
						<div class="errorSourceCode" v-if="isErrorDetailAvailable && isStackTraceAvailable">
							<div class="errorSourceFileNameLine">
								{{errorDetails.fileName + ':' + errorDetails.lineNumber}}
							</div>
							<div class="errorLine" v-for="(text,lineNo) in errorDetails.code">
								<div class="sourceErrorLineNo">
									{{lineNo}}
								</div>
								<div class="sourceErrorLineText" v-bind:class="{hilightLine: errorDetails.lineNumber == lineNo}">
                                    <pre style="solarized-dark" v-highlightjs="text || ' '"><code class="javascript" style="solarized-dark"></code></pre>
								</div>
							</div>
						</div>
	
						<!-- Show Stack Trace -->
						<div class="errorStackTrace" v-if="isErrorDetailAvailable && isStackTraceAvailable">
							<div class="header">
								Stack Trace
							</div>
							<div class="stackTraceLine" v-for="trace in errorDetails.stackTrace">
								at
								<!-- Line Number -->
								<span>
								   {{trace.fileName}}: {{trace.lineNumber}}
								</span>
	
								<!-- Method Name -->
								<span v-if="trace.functionName" style="font-weight: bold;">
									&nbsp;&nbsp;{{trace.functionName}}()
								</span>
							</div>
						</div>
					</div>
	
	
					<!-- No stacktrace details available -->
					<div v-else class="noErrorDetailsAvailable" style="font-weight: bold">
						Message: {{errorDetails.message}}
	
					</div>
	
				</el-tab-pane>
	
	
	
				<!-- Executions Affected By Error -->
				<!-- Error Details -->
				<el-tab-pane class="executionListTab" label="Executions having error" name="executionListTab" v-loading="fetchExecutionsData">
					<!-- RequestDetails List -->
					<!-- Top Pagination -->
					<div class="pagination" v-if="executionList.length > 0">
						<div class="totalCount">Total {{total}} Executions</div>
						<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-sizes="[50, 100, 200]"
							:page-size="50" layout="sizes, prev, pager, next" :total="total">
						</el-pagination>
					</div>

					<div class="executionDetailsContainer" >
						<ul class="list-group">
							<li class="list-group-item list-group-item-action executionItem" v-for="(execution, index) in executionList" :execution="execution" :key="index">
								<div>
									<div>
										<span class="executionId">{{execution.reqId}}</span>
										<span class="float-right" style="color: #F44336;">
									            	ERROR
									            </span>
									</div>
									<div>
										<span class="executionHttpInfo">{{getRequestDisplayDate(execution.timestamp)}}</span>
										<a class="detailsLink" :href="getExecutionDetailsLink(execution.reqId, execution.timestamp)">View Details</a>
									</div>
								</div>
	
							</li>
						</ul>
					</div>
					<!-- Bottom Pagination -->
					<div class="pagination" v-if="executionList.length > 0">
						<div class="totalCount">Total {{total}} Executions</div>
						<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-sizes="[50, 100, 200]"
							:page-size="50" layout="sizes, prev, pager, next" :total="total">
						</el-pagination>
					</div>
	
				</el-tab-pane>
	
			</el-tabs>
		</div>
	
	</div>
</template>

<style lang="scss">
	#errorDetailsPage {
		padding-top: 30px;
		min-height: 400px;
		padding-bottom: 30px;
		margin: auto;
		.errorDetailsHeader {
			font-size: 18px;
			color: #607d8b;
			line-height: 25px;
		}
		.errorDetails {
			margin-top: 20px;
			.el-tabs__content {
				padding: 0px;
			}
			.notFoundTabMessage {
				width: 100%;
				height: 300px;
				padding-top: 135px;
				text-align: center;
				color: #777;
			}
			.errorDetailsTab {
				$borderColor: #9999993d;
				$backgroundColor: #607d8b1a;
				padding: 15px;
				.errorMessage {
					color: #48453b;
					padding-top: 15px;
					padding-bottom: 15px;
				}
				.errorSourceCode {
					border: 1px solid $borderColor;
					border-radius: 3px;
					overflow: hidden;
					font-size: 13px;
					color: #555;
					.errorSourceFileNameLine {
						background: $backgroundColor;
						border-bottom: 1px solid $borderColor;
						padding: 7px;
						padding-left: 10px;
					}
					.errorLine {
						height: 24px;
						/* background: #f0f0f0; */
						.sourceErrorLineNo {
							float: left;
							background: $backgroundColor;
							min-width: 40px !important;
							text-align: right;
							border-right: 1px solid $borderColor;
							color: #555;
							padding: 3px 7px 3px 7px;
							vertical-align: top;
							height: 24px;
						}
						.sourceErrorLineText {
							padding: 0px;
							pre {
								margin: 0px !important;
								padding: 0px !important;
								code {
									background: white;
								}
							}
						}
						.hilightLine {
							pre {
								code {
									background: #ffc10747 !important;
								}
							}
						}
					}
				}
				.errorStackTrace {
					border: 1px solid $borderColor;
					border-radius: 3px;
					overflow: hidden;
					font-size: 12px;
					line-height: 23px;
					overflow-x: auto;
					color: #555;
					margin-top: 30px;
					.header {
						background: $backgroundColor;
						font-weight: bold;
						border-bottom: 1px splid $borderColor;
						padding: 5px;
						padding: 7px;
						padding-left: 10px;
					}
					.stackTraceLine {
						padding: 3px;
						border-top: 1px solid #dddddd73;
						padding-left: 10px !important;
					}
				}
				.noErrorDetailsAvailable {
					height: 170px;
					text-align: center;
					vertical-align: middle;
					padding-top: 80px;
					color: #aaa;
				}
			}
			.errorDetailsTab {
				.errorDetailDataContainer {
					margin-top: 25px;
					padding: 25px;
					color: #555;
					.errorDetailDataTitle {
						font-size: 18px;
						font-weight: bold;
						margin-bottom: 5px;
					}
					.errorData {
						.errorDetailItem {
							font-size: 13px;
							.errorDetailItemLabel {
								margin-top: 10px;
								color: black;
							}
							.errorDetailItemValue {
								color: #777;
								word-wrap: break-word;
							}
						}
					}
				}
			}
			.executionDetailsContainer {
				// min-height: 400px;
				.executionItem {
					cursor: pointer;
					font-size: 12px;
					color: #607d8b;
					line-height: 20px;
					.executionId {
						font-size: 16px;
					}
					.detailsLink {
						color: #4f9ef3;
						font-size: 12px;
						margin-left: 10px;
					}
					.executionHttpInfo {
						color: #999;
					}
				}
			}
		}
	}
</style>

<script>
	import ErrorDetailsComponent from "./errorDetailsComponent";
	export default ErrorDetailsComponent;
</script>


